.rune-step{padding:2rem}.rune-step h2{font-size:1.5rem;margin-bottom:1.5rem;color:#333}.rune-step .rune-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(100px, 1fr));gap:1rem;margin-bottom:2rem}.rune-step .rune-grid .rune-tile{background-color:#f8f8f8;border:2px solid rgba(0,0,0,0);padding:1rem;border-radius:10px;font-size:1rem;text-align:center;cursor:pointer;transition:all .2s ease-in-out;box-shadow:0 2px 5px rgba(0,0,0,.1)}.rune-step .rune-grid .rune-tile:hover:not(:disabled){background-color:#e3f2fd;border-color:#2196f3}.rune-step .rune-grid .rune-tile.selected{background-color:#d0f0c0;border-color:#4caf50;cursor:default}.rune-step .rune-grid .rune-tile:disabled{opacity:.6;cursor:not-allowed}.rune-step .selected-display{margin-bottom:2rem;background-color:#fffbe6;padding:1rem 1.5rem;border:1px solid khaki;border-radius:10px}.rune-step .selected-display h3{margin-bottom:.75rem;color:#444}.rune-step .selected-display ul{list-style-type:none;padding-left:0}.rune-step .selected-display ul li{font-size:1rem;margin-bottom:.5rem;display:flex;align-items:center}.rune-step .selected-display ul li::before{content:"âœ¨";margin-right:.5rem}.rune-step .selected-display .btn.reset{margin-top:1rem;padding:.5rem 1rem;border:none;background-color:#ff9800;color:#fff;border-radius:5px;font-weight:bold;cursor:pointer;transition:background .2s}.rune-step .selected-display .btn.reset:hover{background-color:#e68900}.rune-step .form-nav{display:flex;justify-content:space-between}.rune-step .form-nav .btn{padding:.6rem 1.5rem;font-size:1rem;border:none;border-radius:6px;cursor:pointer;font-weight:bold;transition:background-color .2s}.rune-step .form-nav .btn.back{background-color:#b0bec5;color:#fff}.rune-step .form-nav .btn.back:hover{background-color:#90a4ae}.rune-step .form-nav .btn.next{background-color:#1976d2;color:#fff}.rune-step .form-nav .btn.next:hover{background-color:#1565c0}